@import url(https://fonts.googleapis.com/css2?family=Poppins&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
#root {
  font-family: "Poppins", sans-serif;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.fat-nav > li {
  border-bottom: 1px solid black;
  padding: 10px 0;
  height: 45px;
}
.fat-nav li a {
  display: flex;
  align-items: center;
  color: rgb(182, 179, 179);
  font-size: 14px;
  letter-spacing: 1.5px;
}
.fat-nav li a .fa {
  font-size: 1.4rem;
}
.fat-nav a p:nth-child(1) {
  width: 20%;
  text-align: right;
  margin: 0;
}
.fat-nav a p:nth-child(2) {
  width: 80%;
  text-align: left;
  padding-left: 20px;
  margin: 0;
}

.fat-nav a p:nth-child(2):hover {
  padding-left: 30px;
  transition: padding-left 0.4s;
}
.social a {
  color: white;
  font-size: 1.5rem;
}

.homepage {
  background-image: url(/static/media/bg.4a3f56e1.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
}
.homepage::before {
  position: absolute;
  content: "";
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #181818;
  opacity: 0.6;
}
.particle-container {
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  overflow: hidden;
  z-index: 10;
}

.content {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 100;
}

